import React, { useState, useEffect, useRef, useCallback, useMemo } from 'react';
import './WorkspaceArea.css';

const WorkspaceArea = () => {
  const [widgets, setWidgets] = useState([]);
  const [draggedWidget, setDraggedWidget] = useState(null);
  const [resizingWidget, setResizingWidget] = useState(null);
  const [dragOffset, setDragOffset] = useState({ x: 0, y: 0 });
  const [resizeStart, setResizeStart] = useState({ x: 0, y: 0 });
  const workspaceRef = useRef(null);

  // Widget types with their default configurations
  const widgetTypes = useMemo(() => ({
    charts: {
      type: 'charts',
      title: 'Charts',
      icon: '📊',
      minWidth: 300,
      minHeight: 200,
      content: 'Chart widget content will be displayed here'
    },
    positions: {
      type: 'positions',
      title: 'Positions',
      icon: '📈',
      minWidth: 280,
      minHeight: 180,
      content: 'Positions widget content will be displayed here'
    },
    watchlist: {
      type: 'watchlist',
      title: 'Watchlist',
      icon: '👁️',
      minWidth: 250,
      minHeight: 200,
      content: 'Watchlist widget content will be displayed here'
    },
    trades: {
      type: 'trades',
      title: 'Trades / Transactions',
      icon: '💱',
      minWidth: 300,
      minHeight: 180,
      content: 'Trades widget content will be displayed here'
    }
  }), []);

  // Update widget position
  const updateWidgetPosition = useCallback((widgetId, x, y) => {
    setWidgets(prev => prev.map(widget => 
      widget.id === widgetId ? { ...widget, x, y } : widget
    ));
  }, []);

  // Update widget size
  const updateWidgetSize = useCallback((widgetId, width, height) => {
    setWidgets(prev => prev.map(widget => 
      widget.id === widgetId ? { ...widget, width, height } : widget
    ));
  }, []);

  // Bring widget to front
  const bringWidgetToFront = useCallback((widgetId) => {
    setWidgets(prev => {
      const maxZIndex = Math.max(...prev.map(w => w.zIndex));
      return prev.map(widget => 
        widget.id === widgetId 
          ? { ...widget, zIndex: maxZIndex + 1 }
          : widget
      );
    });
  }, []);



  // Find a position that doesn't overlap with existing widgets
  const findNonOverlappingPosition = useCallback((width, height) => {
    const padding = 20;
    let x = padding;
    let y = padding;
    let attempts = 0;
    const maxAttempts = 200;

    while (attempts < maxAttempts) {
      let hasOverlap = false;
      
      for (const widget of widgets) {
        if (x < widget.x + widget.width + padding &&
            x + width + padding > widget.x &&
            y < widget.y + widget.height + padding &&
            y + height + padding > widget.y) {
          hasOverlap = true;
          break;
        }
      }

      if (!hasOverlap) {
        return { x, y };
      }

      x += 50;
      if (x > 600) {
        x = padding;
        y += 50;
      }
      attempts++;
    }

    // Fallback to a random position
    return {
      x: Math.random() * 300 + 50,
      y: Math.random() * 200 + 50
    };
  }, [widgets]);

  // Add a new widget to the workspace
  const addWidget = useCallback((widgetType) => {
    const widgetConfig = widgetTypes[widgetType];
    if (!widgetConfig) return;

    // Get workspace dimensions
    const workspaceWidth = workspaceRef.current?.clientWidth || 800;
    const workspaceHeight = workspaceRef.current?.clientHeight || 600;
    
    // Calculate default widget size
    const defaultWidth = Math.min(400, workspaceWidth * 0.8);
    const defaultHeight = Math.min(300, workspaceHeight * 0.6);
    
    // Find non-overlapping position
    const position = findNonOverlappingPosition(defaultWidth, defaultHeight);
    
    const newWidget = {
      id: `widget-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`,
      ...widgetConfig,
      x: position.x,
      y: position.y,
      width: defaultWidth,
      height: defaultHeight,
      zIndex: widgets.length + 1
    };

    setWidgets(prev => [...prev, newWidget]);
  }, [widgets.length, widgetTypes, findNonOverlappingPosition]);



  // Handle mouse down for dragging
  const handleMouseDown = (e, widget, action = 'drag') => {
    e.preventDefault();
    e.stopPropagation();
    
    if (action === 'drag') {
      setDraggedWidget(widget);
      setDragOffset({
        x: e.clientX - widget.x,
        y: e.clientY - widget.y
      });
    } else if (action.startsWith('resize-')) {
      setResizingWidget({ ...widget, resizeDirection: action });
      setResizeStart({
        x: e.clientX,
        y: e.clientY,
        startX: widget.x,
        startY: widget.y,
        startWidth: widget.width,
        startHeight: widget.height
      });
    }
  };

  // Check if a position would overlap with other widgets
  const checkOverlap = useCallback((widgetId, x, y, width, height) => {
    return widgets.some(widget => {
      if (widget.id === widgetId) return false;
      
      return x < widget.x + widget.width &&
             x + width > widget.x &&
             y < widget.y + widget.height &&
             y + height > widget.y;
    });
  }, [widgets]);

  // Handle mouse move for dragging and resizing
  const handleMouseMove = useCallback((e) => {
    if (draggedWidget) {
      const newX = e.clientX - dragOffset.x;
      const newY = e.clientY - dragOffset.y;
      
      // Constrain to workspace boundaries
      const constrainedX = Math.max(0, Math.min(newX, workspaceRef.current.clientWidth - draggedWidget.width));
      const constrainedY = Math.max(0, Math.min(newY, workspaceRef.current.clientHeight - draggedWidget.height));
      
      // Check for overlap with other widgets
      if (!checkOverlap(draggedWidget.id, constrainedX, constrainedY, draggedWidget.width, draggedWidget.height)) {
        updateWidgetPosition(draggedWidget.id, constrainedX, constrainedY);
      }
    }
    
    if (resizingWidget) {
      const deltaX = e.clientX - resizeStart.x;
      const deltaY = e.clientY - resizeStart.y;
      
      let newX = resizeStart.startX;
      let newY = resizeStart.startY;
      let newWidth = resizeStart.startWidth;
      let newHeight = resizeStart.startHeight;
      
      // Handle different resize directions
      switch (resizingWidget.resizeDirection) {
        case 'resize-nw': // North-west corner
          newX = resizeStart.startX + deltaX;
          newY = resizeStart.startY + deltaY;
          newWidth = resizeStart.startWidth - deltaX;
          newHeight = resizeStart.startHeight - deltaY;
          break;
        case 'resize-ne': // North-east corner
          newY = resizeStart.startY + deltaY;
          newWidth = resizeStart.startWidth + deltaX;
          newHeight = resizeStart.startHeight - deltaY;
          break;
        case 'resize-sw': // South-west corner
          newX = resizeStart.startX + deltaX;
          newWidth = resizeStart.startWidth - deltaX;
          newHeight = resizeStart.startHeight + deltaY;
          break;
        case 'resize-se': // South-east corner
          newWidth = resizeStart.startWidth + deltaX;
          newHeight = resizeStart.startHeight + deltaY;
          break;
        case 'resize-n': // North edge
          newY = resizeStart.startY + deltaY;
          newHeight = resizeStart.startHeight - deltaY;
          break;
        case 'resize-s': // South edge
          newHeight = resizeStart.startHeight + deltaY;
          break;
        case 'resize-w': // West edge
          newX = resizeStart.startX + deltaX;
          newWidth = resizeStart.startWidth - deltaX;
          break;
        case 'resize-e': // East edge
          newWidth = resizeStart.startWidth + deltaX;
          break;
        default:
          // No resize direction specified
          break;
      }
      
      // Apply minimum size constraints
      const constrainedWidth = Math.max(resizingWidget.minWidth, newWidth);
      const constrainedHeight = Math.max(resizingWidget.minHeight, newHeight);
      
      // Constrain to workspace boundaries
      const maxX = workspaceRef.current.clientWidth - constrainedWidth;
      const maxY = workspaceRef.current.clientHeight - constrainedHeight;
      const constrainedX = Math.max(0, Math.min(newX, maxX));
      const constrainedY = Math.max(0, Math.min(newY, maxY));
      
      // Check for overlap with other widgets
      if (!checkOverlap(resizingWidget.id, constrainedX, constrainedY, constrainedWidth, constrainedHeight)) {
        updateWidgetPosition(resizingWidget.id, constrainedX, constrainedY);
        updateWidgetSize(resizingWidget.id, constrainedWidth, constrainedHeight);
      }
    }
  }, [draggedWidget, resizingWidget, dragOffset, resizeStart, updateWidgetPosition, updateWidgetSize, checkOverlap]);

  // Handle mouse up to stop dragging/resizing
  const handleMouseUp = useCallback(() => {
    if (draggedWidget) {
      // Bring widget to front
      bringWidgetToFront(draggedWidget.id);
      setDraggedWidget(null);
    }
    
    if (resizingWidget) {
      setResizingWidget(null);
    }
  }, [draggedWidget, resizingWidget, bringWidgetToFront]);



  // Remove widget
  const removeWidget = (widgetId) => {
    setWidgets(prev => prev.filter(widget => widget.id !== widgetId));
  };

  // Add event listeners for mouse move and up
  useEffect(() => {
    if (draggedWidget || resizingWidget) {
      document.addEventListener('mousemove', handleMouseMove);
      document.addEventListener('mouseup', handleMouseUp);
      
      return () => {
        document.removeEventListener('mousemove', handleMouseMove);
        document.removeEventListener('mouseup', handleMouseUp);
      };
    }
  }, [draggedWidget, resizingWidget, dragOffset, resizeStart, handleMouseMove, handleMouseUp]);

  // Expose addWidget function to parent component
  useEffect(() => {
    if (window.addWidgetToWorkspace) {
      window.addWidgetToWorkspace = addWidget;
    } else {
      window.addWidgetToWorkspace = addWidget;
    }
  }, [addWidget]);

  return (
    <div 
      className="workspace-area-container"
      ref={workspaceRef}
      onMouseMove={handleMouseMove}
      onMouseUp={handleMouseUp}
    >
      {widgets.length === 0 ? (
        <div className="workspace-empty-state">
          <div className="workspace-title">Workspace Area</div>
          <div className="workspace-subtitle">
            Ready to build your custom workspace from scratch
          </div>
          <div className="workspace-instructions">
            Use the + button in the sidebar to add widgets. Widgets automatically avoid overlap and can be freely moved and resized.
          </div>
        </div>
      ) : (
        widgets.map(widget => (
          <div
            key={widget.id}
            className={`workspace-widget workspace-widget-${widget.type}`}
            style={{
              left: widget.x,
              top: widget.y,
              width: widget.width,
              height: widget.height,
              zIndex: widget.zIndex
            }}
          >
            {/* Widget Header */}
            <div 
              className="widget-header"
              onMouseDown={(e) => handleMouseDown(e, widget, 'drag')}
            >
              <div className="widget-title">
                <span className="widget-icon">{widget.icon}</span>
                {widget.title}
              </div>
              <div className="widget-controls">
                <button 
                  className="widget-control-btn widget-minimize"
                  title="Minimize"
                >
                  −
                </button>
                <button 
                  className="widget-control-btn widget-close"
                  title="Close"
                  onClick={() => removeWidget(widget.id)}
                >
                  ×
                </button>
              </div>
            </div>
            
            {/* Widget Content */}
            <div className="widget-content">
              {widget.content}
            </div>
            
                         {/* Resize Handles - All corners and edges */}
             <div 
               className="widget-resize-handle widget-resize-handle-nw"
               onMouseDown={(e) => handleMouseDown(e, widget, 'resize-nw')}
             />
             <div 
               className="widget-resize-handle widget-resize-handle-ne"
               onMouseDown={(e) => handleMouseDown(e, widget, 'resize-ne')}
             />
             <div 
               className="widget-resize-handle widget-resize-handle-sw"
               onMouseDown={(e) => handleMouseDown(e, widget, 'resize-sw')}
             />
             <div 
               className="widget-resize-handle widget-resize-handle-se"
               onMouseDown={(e) => handleMouseDown(e, widget, 'resize-se')}
             />
             <div 
               className="widget-resize-handle widget-resize-handle-n"
               onMouseDown={(e) => handleMouseDown(e, widget, 'resize-n')}
             />
             <div 
               className="widget-resize-handle widget-resize-handle-s"
               onMouseDown={(e) => handleMouseDown(e, widget, 'resize-s')}
             />
             <div 
               className="widget-resize-handle widget-resize-handle-w"
               onMouseDown={(e) => handleMouseDown(e, widget, 'resize-w')}
             />
             <div 
               className="widget-resize-handle widget-resize-handle-e"
               onMouseDown={(e) => handleMouseDown(e, widget, 'resize-e')}
             />
          </div>
        ))
      )}
    </div>
  );
};

export default WorkspaceArea;
